import React from 'react';
import { Row, Col } from 'antd';
import { createMainCard, createSideCard } from './common';
import { 
  forums, eosdocs, eosblogs, eostools, ipfs, polkadot, apis, ides, githubs, 
  sites, resources, tools, 
} from '../data/developer';

const DeveloperPage = () => (
  <Row gutter={24}>
    <Col xl={15} lg={24} md={24} sm={24} xs={24}>
      {createMainCard('', forums)}
      {createMainCard('EOS', eosdocs)}
      {createMainCard('EOS', eosblogs)}
      {createMainCard('EOS', eostools)}
      {createMainCard('IPFS &  & ', ipfs)}
      {createMainCard(' &  & ', polkadot)}
      {createMainCard('API & ', apis)}
      {createMainCard('IDE & ', ides)}
      {createMainCard('Github', githubs)}
    </Col>

    <Col xl={9} lg={24} md={24} sm={24} xs={24}>
      {createSideCard('', sites)}
      {createSideCard('', resources)}
      {createSideCard('', tools)}
    </Col>
  </Row>
)

export default DeveloperPage;