import React from 'react';
import { Row, Col } from 'antd';
import { createSideCard, createMainCard } from '../common';
import { 
  navigations, infos, forums, evaluations, 
  exchanges, dxchanges, oxchanges, 
  tools, resources, browsers, icos, candies, 
} from '../../data/home';

const HomePage = () => (
  <Row gutter={24}>
    <Col xl={15} lg={24} md={24} sm={24} xs={24}>
      {createMainCard('', navigations)}
      {createMainCard('', infos)}
      {createMainCard(' & ', forums)}
      {createMainCard('', evaluations)}
      {
        createMainCard('', exchanges, 
          <span>
            <a href="https://www.feixiaohao.com/exchange/notice" target="_blank" rel="noopener noreferrer">&nbsp;|&nbsp;</a>
            <a href="https://www.mytoken.io/media/notice" target="_blank" rel="noopener noreferrer">MyToken&nbsp;|&nbsp;</a>
            <a href="https://block.cc/notice" target="_blank" rel="noopener noreferrer"></a>
          </span>)
      }
      {createMainCard('', dxchanges)}
      {createMainCard('', oxchanges)}
    </Col>

    <Col xl={9} lg={24} md={24} sm={24} xs={24}>
      {createSideCard('', tools)}
      {createSideCard('', resources)}
      {createSideCard('ICO & IEO', icos)}
      {createSideCard('', browsers)}
      {createSideCard('', candies)}
    </Col>
  </Row>
)

export default HomePage;